.movie-detail {
    padding: 20px;
  }
  
  .movie-detail-container {
    display: flex;
    gap: 30px;
  }
  
  .movie-image {
    flex-shrink: 0;
    width: 600px;
  }
  
  .movie-image img {
    width: 100%;
    border-radius: 8px;
  }
  
  .movie-info {
    flex: 1;
  }
  
  .movie-title {
    margin-bottom: 20px;
    font-size: 48px;
  }
  
  .movie-meta {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    font-size: 32px;
  }

  .star-icon {
    margin-right: 5px;
    width: 54px;
    height: 54px;
  }

  .movie-description {
    font-size: 32px;
    line-height: 1.5;
    margin-bottom: 20px;
  }

  .movie-release-date {
    font-size: 32px;
  }

  .add-to-list-button {
    padding: 20px 40px;
    font-size: 24px;
    width: 400px;
    height: 120px;
    background-color: #3f51b5;
    color: white;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  .add-to-list-button:hover {
    background-color: #303f9f;
    transform: translateY(-2px);
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
  }