.user-lists {
    margin-top: 20px;
  }
  
  .list-container {
    margin-bottom: 10px;
  }
  
  .list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #f5f5f5;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .list-movies {
    padding: 10px;
  }
  
  .list-movie-item {
    padding: 5px;
    cursor: pointer;
  }
  
  .list-movie-item:hover {
    background-color: #f5f5f5;
  }