.recent-movies {
    margin-top: 20px;
  }
  
  .recent-movie-item {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  
  .recent-movie-item:hover {
    background-color: #f5f5f5;
  }
  
  .movie-poster {
    width: 40px;
    height: 60px;
    margin-right: 15px;
  }
  
  .movie-poster img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
  }

  .recent-movie-title {
    font-size: 16px;
    font-weight: 500;
    color: #333;
  }