.main-content {
    flex: 1;
    padding: 20px;
    background-color: #f5f5f5;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
  }
  
  .logo h1 {
    margin: 0;
    cursor: pointer;
  }
  
  .header-buttons {
    display: flex;
    gap: 12px;
    align-items: center;
  }

  .profile-button {
    cursor: pointer;
    padding: 10px 20px;
    background-color: #3f51b5;
    border: none;
    border-radius: 8px;
    transition: background-color 0.2s;
  }

  .profile-button:hover {
    background-color: #303f9f;
  }

  .sign-out-button {
    cursor: pointer;
    padding: 10px 20px;
    background-color: #f44336;
    border: none;
    border-radius: 8px;
    transition: all 0.3s ease;
  }

  .sign-out-button:hover {
    background-color: #d32f2f;
    transform: translateY(-2px);
  }

  .profile-text, .sign-out-text {
    color: white;
    font-weight: bold;
    font-size: 16px;
  }
  
  .content-area {
    background-color: white;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }